/**
 * Shopistry PWA 
*/
.site__logo {
  position: relative;
  margin: auto;
  max-width: 180px;
  text-align: center;
  top: -10px; }
  .site__logo img {
    max-width: 100%; }

nav {
  width: 100%;
  height: 60px;
  color: #FFF;
  background: #eee;
  position: fixed;
  top: 0px;
  font-size: 20px;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 2px 9px 1px rgba(0, 0, 0, 0.12), 0 4px 2px -2px rgba(0, 0, 0, 0.2);
  padding: 16px 16px 0 16px;
  will-change: transform;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  transition: transform 0.233s cubic-bezier(0, 0, 0.21, 1) 0.1s;
  z-index: 10; }
  nav li {
    list-style: none;
    margin-right: 30px; }
    nav li a {
      text-decoration: none;
      color: #333; }
  nav .float__items {
    position: absolute;
    right: 0px;
    width: 200px;
    display: flex;
    justify-content: space-between; }

.header__menu {
  position: absolute;
  left: 20px;
  width: 24px;
  height: 24px;
  top: 10px;
  display: none; }
  @media (max-width: 980px) {
    .header__menu {
      display: block; } }
  .header__menu div {
    width: 24px;
    height: 3px;
    background-color: #eeb744;
    margin: 5px 0;
    border-radius: 8px; }
