.loader__bg {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 99;
  background-color: rgba(255, 255, 255, 0.5); }

.page__loader {
  position: absolute;
  top: 40%;
  transform: translateY(-50%);
  left: 0px;
  right: 0px;
  margin: auto;
  text-align: center;
  z-index: 99; }
  .page__loader .lds-ring {
    display: inline-block;
    position: absolute;
    width: 64px;
    height: 64px; }
  .page__loader .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 51px;
    height: 51px;
    margin: 6px;
    border: 6px solid #000;
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #000 transparent transparent transparent; }
  .page__loader .lds-ring div:nth-child(1) {
    animation-delay: -0.45s; }
  .page__loader .lds-ring div:nth-child(2) {
    animation-delay: -0.3s; }
  .page__loader .lds-ring div:nth-child(3) {
    animation-delay: -0.15s; }

@keyframes lds-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
